<script>
export default {
  // preparo Footer per essere esportato
  name: "FooterComp",
  data() {
    return {
      showFooter: false,
      lastScrollPosition: 0,
      showMenu: false,
      links: [
        {
          text: "home",
          url: "/",
          icon: "house-user",
          current: false,
        },
        {
          text: "about",
          url: "/about",
          icon: "user",
          current: false,
        },
        {
          text: "my projects",
          url: "/projects",
          icon: "window-restore",
          current: false,
        },
      ],
      projects: [
        {
          text: "bookshock",
          url: "/projects/bookshock",
          current: false,
        },
        {
          text: "boolflix",
          url: "/projects/boolflix",
          //icon: "clapperboard",
          current: false,
        },
        {
          text: "weatherdb",
          url: "/projects/weatherdb",
          //icon: "cloud-moon-rain",
          current: false,
        },
        {
          text: "calendar",
          url: "/projects/calendar",
          //icon: "cloud-moon-rain",
          current: false,
        },
        {
          text: "todolist",
          url: "/projects/todolist",
          //icon: "cloud-moon-rain",
          current: false,
        },
        {
          text: "memory game",
          url: "/projects/memory",
          //icon: "cloud-moon-rain",
          current: false,
        },
        {
          text: "tic-tac-toe",
          url: "/projects/tictactoe",
          //icon: "cloud-moon-rain",
          current: false,
        },
        {
          text: "quiz game",
          url: "/projects/quizgame",
          //icon: "cloud-moon-rain",
          current: false,
        },
      ],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    // funzione che nasconde il menu allo scrollDown e lo fa riapparire allo scrollUp (solo sopra width 800)
    onScroll() {
      var viewport_width = window.innerWidth;
      const currentScrollPosition = window.pageYOffset;
      if (viewport_width > 800) {
        // prendo il valore corrente di currentScrollPosition
        if (currentScrollPosition < 200) {
          this.showFooter = false;
        }
        // Stoppa l'esecuzione della funzione se la differenza tra currentScrollPosition e lastScrollPosition  minore dell'offset (10)
        if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 0) {
          return;
        }
        this.showFooter = currentScrollPosition < this.lastScrollPosition;
        // Setto il valore di currentScrollPosition con il valore di lastScrollPosition
        this.lastScrollPosition = currentScrollPosition;
      } else {
        this.showFooter = false;
      }
    },
    toggle() {
      this.showMenu = !this.showMenu;
    },
    hideMenu() {
      this.showMenu = false;
    },
    faClass(icon) {
      return `fa-solid fa-${icon}`;
    },
  },
};
</script>

<style lang="scss" scoped>
/* // importo gli stili */
@import "../assets/style/app.scss";
