<template>
  <div 
    id="carousel"
    :style="childStyle"
  > 
    <spring-spinner
    v-if="compIsLoading"
    :animation-duration="3000"
    :size="60"
    color="#f5d300"
    />
    <carousel-3d
    v-else
      :disable3d="isDisable"
      :space="365"
      :clickable="false"
      :controls-visible="false"
      :autoplay="true"
      :autoplay-timeout="5000"
    >
      <slide
        v-for="slide in slides"
        :key="slide.id"
        :index="slide.id"
        :class="slide.name"
      >
        <router-link :to="slide.routerLink">
          <img
            :src="require(`../assets/img/logos/${slide.name}.png`)"
            :alt="slide.name"
          />
        </router-link>
      </slide>
    </carousel-3d>
  </div>
</template>

<script>
// YourComponent.vue

import { Carousel3d, Slide } from "vue-carousel-3d";
import { SpringSpinner } from 'epic-spinners'

export default {
  name: "CarouselComp",
  components: {
    Carousel3d,
    Slide,
    SpringSpinner
  },
  data() {
    return {
      compIsLoading: false,
      slides: {
        boolflix: {
          id: 0,
          name: "boolflix",
          routerLink: "/projects/boolflix",
        },
        bookshok: {
          id: 1,
          name: "bookshock",
          routerLink: "/projects/bookshock",
        },
        games_db: {
          id: 2,
          name: "games_db",
          routerLink: "/projects/gamesdb",
        },
        memory_rick_and_morty: {
          id: 3,
          name: "memory",
          routerLink: "/projects/memory",
        },
        tic_tac_toe: {
          id: 4,
          name: "tic_tac_toe",
          routerLink: "/projects/tictactoe",
        },
        quizgame: {
          id: 5,
          name: "quizgame",
          routerLink: "/projects/quizgame",
        },
        calendar: {
          id: 6,
          name: "calendar",
          routerLink: "/projects/calendar",
        },
        todolist: {
          id: 7,
          name: "todolist",
          routerLink: "/projects/todolist",
        },
        geocoder: {
          id: 8,
          name: "geocoder",
          routerLink: "/projects/geocoder",
        },
        mbg: {
          id: 9,
          name: "mbg",
          routerLink: "/projects/mbg",
        },
      },
    };
  },
  props: {
    childStyle: {
      type: Object,
      required: false,
    },
    isDisable: {
      type: Boolean,
      default: false
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {
    this.compIsLoading = true;
  },
  mounted() {
    this.compIsLoading = false;
  },
  beforeUpdate() {},
  updated() {},
  beforeUnmount() {},
  unmounted() {},
};
</script>

<!-- Use preprocessors via the lang attribute! e.g. <style lang="scss"> -->
<style lang="scss" scoped>
@import "../assets/style/app.scss";

#carousel {
  @include flex($justify-content: center, $align-items: center);
  height: 80%;

  .boolflix,
  .weatherdb,
  .bookshock,
  .calendar,
  .memory,
  .tic_tac_toe,
  .todolist,
  .quizgame,
  .games_db,
  .geocoder,
  .mbg {
    @include flex($justify-content: center, $align-items: center);
    background: linear-gradient(to right, rgb(22, 22, 21), rgba(0, 0, 0, 0.644)),
      url(@/assets/img/pattern1.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  img {
    width: 200px;
  }
}
</style>
