<script>
const date = new Date();
export default {
  name: "LandingPageComp",
  data() {
    return {
      morning: false,
      afternoon: false,
      evening: false,
      welcome: false,
      // interval: null,
      // time: null,
      dateTime: {
        hours: date.getHours(),
        minutes: date.getMinutes(),
        seconds: date.getSeconds(),
      },
      timer: undefined,
    };
  },
  methods: {
    currentDate() {
      const date = new Date();
      switch (date.getDay()) {
        case 0:
          return `Sunday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 1:
          return `Monday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 2:
          return `Tuesday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 3:
          return `Wednesday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 4:
          return `Thursday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 5:
          return `Friday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        case 6:
          return `Saturday, ${date.getDate()}.${
            date.getMonth() + 1
          }.${date.getFullYear()}`;
        default:
          "";
          break;
      }
    },
    setDateTime() {
      const date = new Date();
      this.dateTime = {
        hours: date.getHours(),
        minutes: date.getMinutes(),
        seconds: date.getSeconds(),
      };
    },
    greeting() {
      var hours = new Date().getHours();
      //console.log(hours);
      if (hours >= 6 && hours < 12) {
        this.morning = true;
        return "Good Morning";
      } else if (hours >= 12 && hours < 18) {
        this.afternoon = true;
        return "Good Afternoon";
      } else if (hours >= 18 && hours < 23) {
        this.evening = true;
        return "Good Evening";
      } else {
        this.welcome = true;
        return "Welcome";
      }
    },
    scrollMeTo() {
      this.$emit("scroll-me-to");
    },
  },
  beforeMount() {
    this.timer = setInterval(this.setDateTime, 1000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
};
</script>
