<template>
  <div id="menu">
    <input type="checkbox" id="menu-toggle" />
    <ul>
      <li
        v-for="(link, index) in links"
        :key="index"
        @click.stop=""
        :class="{ active: link.current }"
      >
        <router-link :to="link.url">
          <i class="my_i" :class="[faClass(link.icon)]"></i>
          {{ link.text }}
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "HeaderComp",
  data() {
    return {
      showCollapse: false,
      links: [
        {
          text: "home",
          url: "/",
          icon: "house-user",
          current: false,
        },
        {
          text: "about",
          url: "/about",
          icon: "user",
          current: false,
        },
        {
          text: "my projects",
          url: "/projects",
          icon: "window-restore",
          current: false,
        },
      ],
    };
  },
  methods: {
    faClass(icon) {
      return `fa-solid fa-${icon}`;
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/style/app.scss";
#menu {
  @media all and (max-width: 992px) {
    display: none;
  }
  position: fixed;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 500;
  height: 50px;
  border-radius: 25px;
  overflow: hidden;
  // background: #444;
  background: #20232a7c;
  // box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);

  transition: all 0.6s ease-in-out;
  &:hover {
    background: rgba(0, 0, 0, 0.678);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  }
  & > * {
    float: left;
  }
}

#menu-toggle {
  display: block;
  cursor: pointer;
  opacity: 0;
  z-index: 999;
  margin: 0;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  &:checked ~ ul {
    width: 350px;
    background-position: 0px -50px;
  }
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 50px;
  height: 50px;
  width: 0px;
  transition: 0.5s width ease;
  background-image: url("../assets/img/menu.png");
  background-repeat: no-repeat;
  background-position: 0px 0px;
  li {
    display: inline-block;
    padding-right: 20px;
    line-height: 50px;
    //Swidth: 80px;
    text-align: center;
    margin: 0;
    a.router-link-exact-active {
      color: #f5d300;
    }
  }
}
</style>
